// frontend/src/app/admin/page.tsx
"use client";

import React, { useEffect, useState } from "react";
import axios from "axios";
import { useRouter } from "next/navigation";
import Layout from "../layout";

interface DashboardData {
  totalJobs: number;
  totalApplications: number;
}

const AdminDashboardPage: React.FC = () => {
  const router = useRouter();
  const [loading, setLoading] = useState(true);
  const [data, setData] = useState<DashboardData | null>(null);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchDashboard = async () => {
      try {
        setLoading(true);

        const res = await axios.get("/api/admin/me", { withCredentials: true });

        if (!res.data?.success) {
          router.push("/admin/login");
          return;
        }

        const [jobsRes, appsRes] = await Promise.all([
          axios.get("/api/admin/jobs/count", { withCredentials: true }),
          axios.get("/api/admin/applications/count", { withCredentials: true }),
        ]);

        setData({
          totalJobs: jobsRes.data?.count || 0,
          totalApplications: appsRes.data?.count || 0,
        });
      } catch (err: unknown) {
        if (axios.isAxiosError(err)) {
          console.error(err.response?.data?.message || err.message);
        } else if (err instanceof Error) {
          console.error(err.message);
        } else {
          console.error("Unexpected error:", err);
        }
        setError("Failed to load dashboard. Try again.");
      } finally {
        setLoading(false);
      }
    };

    fetchDashboard();
  }, [router]);

  if (loading) {
    return (
      <Layout>
        <div className="flex items-center justify-center h-[70vh] text-gray-500">
          Loading dashboard...
        </div>
      </Layout>
    );
  }

  if (error) {
    return (
      <Layout>
        <div className="flex items-center justify-center h-[70vh] text-red-500">
          {error}
        </div>
      </Layout>
    );
  }

  return (
    <Layout>
      <div className="p-6">
        <h1 className="text-3xl font-bold mb-6">Admin Dashboard</h1>

        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
          <div className="bg-white shadow rounded-lg p-6 flex flex-col items-start">
            <h2 className="text-lg font-semibold text-gray-600 mb-2">Total Jobs</h2>
            <span className="text-3xl font-bold text-blue-600">{data?.totalJobs}</span>
          </div>

          <div className="bg-white shadow rounded-lg p-6 flex flex-col items-start">
            <h2 className="text-lg font-semibold text-gray-600 mb-2">Total Applications</h2>
            <span className="text-3xl font-bold text-green-600">{data?.totalApplications}</span>
          </div>

          <div className="bg-white shadow rounded-lg p-6 flex flex-col items-start">
            <h2 className="text-lg font-semibold text-gray-600 mb-2">Quick Actions</h2>
            <ul className="text-gray-700 list-disc list-inside">
              <li>Create Job</li>
              <li>Manage Applications</li>
              <li>Edit Pages</li>
            </ul>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default AdminDashboardPage;
